@tailwind base;
@tailwind components;
@tailwind utilities;

.pokeball {
  display: block;
  width: 192px;
  height: 192px;
  background: radial-gradient(
      white 19px 24px,
      black 25px 32px,
      transparent 33px
    ),
    linear-gradient(to bottom, red 0 80px, black 81px 96px, white 97px 100%);
  border-radius: 50%;
  border: 8px solid black;
  box-shadow: inset -16px -8px 0 0 rgba(0, 0, 0, 0.2);
  transform: scale(0.7);
  transition: all 0s;
}
